html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .joinOuterContainer {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    

  }
  
  .joinInnerContainer {
    width: 20%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 1rem;
   
  }
  
  .joinInput {
    border-radius: 0.5rem;
    padding: 15px 20px;
    width: 80%;

  }
  
  .heading {
    color: rgb(0, 0, 0);
    font-size: 2.5em;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
    margin-top: 5%;
  }
  
  .button {
      color: #fff !important;
      text-transform: uppercase;
      text-decoration: none;
      background: #526057 ;
      padding: 20px;
      border-radius: 5px;
      display: inline-block;
      border: none;
      width: 80%;
      margin-bottom: 5%;
  }
  
  .mt-20 {
    margin-top: 20px;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 90%;
    }
  
  
  }
  
  button:focus {
    outline: 0;
  }
.outerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    
  }
  
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 1rem;
    height: 60%;
    width: 35%;
    box-shadow: 0 1px 8px 0 rgba(0,0,0,0.2);
    padding-left: 0px !important;
    padding-right: 0px !important;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .outerContainer {
      height: 100%;
    }
  
    .container {
      width: 100%;
      height: 100%;
    }
  }
  
  @media (min-width: 480px) and (max-width: 1200px) {
    .container {
      width: 60%;
    }
  }

