.lirain{
  position: absolute;
  top: 50%;  left: 50%;
  transform: translate(-50%,-50%);
  width: 450px;  height: 450px;
}
.lirain::after{
  content: '';
  position: absolute;
  left: 0; top: 50%;
  width: 100%;   height: 100%;
  background: #fff;
  border-top: 5px solid rgba(0,0,0,.3);
}
.lirain li{
  list-style: none;
  position: absolute;
  top: 50%;  left: 50%;
  border-radius: 50%;
  border: 15px solid transparent;
  box-shadow: 1px 1px 12px rgb(0 0 0 / 20%);
  animation: animate 5s infinite linear;
}
@keyframes animate{
  0%{
      transform: translate(-50%,-50%) rotate(45deg);
  }
  50%{
      transform: translate(-50%,-50%) rotate(calc(360deg + 45deg));
  }
  60%{
      transform: translate(-50%,-50%) rotate(calc(360deg + 45deg));
  }
  100%{
      transform: translate(-50%,-50%) rotate(45deg);
  }
}
.lirain li:nth-child(8){
  width: 40px;  height: 40px;
  border: none;
}
.lirain li:nth-child(1){
  width: calc(40px + (0 * 15px));  height: calc(40px + (0 * 15px));
