.auth-options {
  margin-top: auto;
  z-index: 3;
}

.auth-options ul {

  list-style: none;
  overflow: hidden;
  
  
}

.auth-options li a {
  display: block;

  text-decoration: none;
}



/* menu */

.auth-options .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
}

/* menu icon */

.auth-options .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.auth-options .menu-icon .navicon {
  background: #333;
  display: block;
  height: 2px;
  position: relative;
  transition: background .2s ease-out;
  width: 18px;
}

.auth-options .menu-icon .navicon:before,
.auth-options .menu-icon .navicon:after {
  background: #333;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}

.auth-options .menu-icon .navicon:before {
  top: 5px;
}

.auth-options .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.auth-options .menu-btn {
  display: none;
}

.auth-options .menu-btn:checked ~ .menu {
  max-height: 340px;
}

.auth-options .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.auth-options .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.auth-options .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.auth-options .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.auth-options .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
  
}



  


/* 48em = 768px */

@media (min-width: 68em) {
  .auth-options li {
    float: left;
  }
  .auth-options .menu {
    clear: none;
    float: right;
    max-height: none;
  }
  .auth-options .menu-icon {
    display: none;
  }

}
